.feed{
    position: relative;
    display: flex;
    flex: 1 1 auto;
    flex-direction: column;
}
.feed-title{
    display: flex;
    flex: 1 1 30px;
    flex-direction: row;
    font-size: 30px;
    margin-left:15px;
}
.feed-card-content{
    margin-top:5px;
    font-weight: normal;
}
.empty{
    position: absolute;
    display: flex;
    flex: 1 1 auto;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width:100%;
    height:100%;
    font-size:11px;
    color:rgba(0,0,0,0.3);
}
